<!-- 条件选品种弹窗 -->
<template>
    <div>
        <Modal :value="isShow" width="500px" :mask-closable="true" @on-cancel="close()">
            <template slot="header">
                <span>{{$t('事件')}}</span>
            </template>
            <div class="f24 clof">{{info.title}}</div>
            <div class="f12 op7 mt5 cloe">{{info.date&&info.date.substr(0,10)}}<span class="ml10">{{ info.source }}</span></div>
            <!-- <div></div> -->
            <div class="f14 mt10 clof">{{info.desc}}</div>
            <template slot="footer">
                <div class="center">
                    <!-- <Button type="info">
                        <span>{{$t('关闭')}}</span>
                    </Button> -->
                </div>
            </template>
        </Modal>
    </div>
</template>

<script>
export default {
    name: 'WebWarnPop',
    model:{
        prop:'isShow',
        event:'change'
    },
    props: {
        //连接钱包弹窗，是否显示
        isShow:{
            type:Boolean,
            default:false
        },
        info:{
            type:Object,
            default:()=>{}
        }
    },
    data() {
        return {
        };
    },
    mounted() {
        
    },
    methods: {
        //关闭
        close(){
            this.$emit("change",false);
        },
    },
};
</script>

<style lang="less" scoped>
:deep(.ivu-modal-header-inner){
    color:#ccc;
}
:deep(.ivu-form-item){
    margin-bottom: 10px;
}
</style>